import React, { useState, useEffect } from "react"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faFilePdf, faFileExcel, faFilePowerpoint, faBackward, faForward, faShareFromSquare, faDownload, faExpand } from "@fortawesome/free-solid-svg-icons"
import * as style from "../styles/Toolbox.module.scss"
import { currentViz, currentCode } from "./TableauViz"
import Tooltip, { onShowTooltip } from "./Tooltip"

export default function Toolbox(props: any) {

    const [codes, setCodes] = useState<string[]>(props.codes)
    const [downloads, setDownloads] = useState<string[]>(props.downloads)

    useEffect(() => {
        document.getElementById(style.faFilePdf).addEventListener("mouseenter", () => { onShowTooltip(style.faFilePdf) })
        document.getElementById(style.faFileExcel).addEventListener("mouseenter", () => { onShowTooltip(style.faFileExcel) })
        document.getElementById(style.faFilePowerpoint).addEventListener("mouseenter", () => { onShowTooltip(style.faFilePowerpoint) })
        document.getElementById("undo").addEventListener("mouseenter", () => { onShowTooltip("undo") })
        document.getElementById("redo").addEventListener("mouseenter", () => { onShowTooltip("redo") })
        document.getElementById("share").addEventListener("mouseenter", () => { onShowTooltip("share") })
        document.getElementById("download").addEventListener("mouseenter", () => { onShowTooltip("download") })
        document.getElementById("fullscreen").addEventListener("mouseenter", () => { onShowTooltip("fullscreen") })
        window.addEventListener("mavQ:resetRed", onResetRed)
        window.addEventListener("mavQ:resetWhite", onResetWhite)
    }, [])

    function onResetRed() {
        document.getElementById("resetDashboard").style.backgroundColor = "#ff6e63"
    }

    function onResetWhite() {
        document.getElementById("resetDashboard").style.backgroundColor = "white"
    }

    return (
        <div id={style.container}>
            <div className={style.shelf}>
                <div className={style.hasTooltip}>
                    <FontAwesomeIcon id={style.faFilePdf} className={style.icon} icon={faFilePdf} onClick={downloadPDF} />
                    <Tooltip elementId={style.faFilePdf} content="Download PDF" />
                </div>
                <div className={style.hasTooltip}>
                    <FontAwesomeIcon id={style.faFileExcel} className={style.icon} icon={faFileExcel} onClick={downloadExcel} />
                    <Tooltip elementId={style.faFileExcel} content="Download Excel" />
                </div>
                <div className={style.hasTooltip}>
                    <FontAwesomeIcon id={style.faFilePowerpoint} className={style.icon} icon={faFilePowerpoint} onClick={downloadPowerPoint} />
                    <Tooltip elementId={style.faFilePowerpoint} content="Download PowerPoint" />
                </div>
            </div>
            <button id="resetDashboard" className={style.resetDashboard} onClick={onResetClicked}><p></p>RESET</button>
            <div className={style.shelf}>
                <div className={style.hasTooltip}>
                    <FontAwesomeIcon className={style.tool} id="undo" icon={faBackward} onClick={onUndoClick} />
                    <Tooltip elementId="undo" content="Undo" />
                </div>
                <div className={style.hasTooltip}>
                    <FontAwesomeIcon className={style.tool} id="redo" icon={faForward} onClick={onRedoClick} />
                    <Tooltip elementId="redo" content="Redo" />
                </div>
                <div className={style.hasTooltip}>
                    <FontAwesomeIcon className={style.tool} id="share" icon={faShareFromSquare} onClick={onShareClick} />
                    <Tooltip elementId="share" content="Share" />
                </div>
                <div className={style.hasTooltip}>
                    <FontAwesomeIcon className={style.tool} id="download" icon={faDownload} onClick={onDownloadClick} />
                    <Tooltip elementId="download" content="Download" />
                </div>
                <div className={style.hasTooltip}>
                    <FontAwesomeIcon className={style.tool} id="fullscreen" icon={faExpand} onClick={() => window.dispatchEvent(new Event("mavQ:onFullscreenClick"))} />
                    <Tooltip elementId="fullscreen" content="Fullscreen Toggle" />
                </div>
            </div>
        </div>
    )
    function onResetClicked() {
        window.dispatchEvent(new CustomEvent("mavQ:resetViz", { detail: { code: currentCode} }))
        let resetButton = document.getElementById("resetDashboard")
        resetButton.style.backgroundColor = "white";
    }
    
    function downloadPDF() {
        currentViz.showExportPDFDialog()
    }
    
    function downloadExcel() {
        currentViz.exportCrossTabToExcel(downloads[codes.indexOf(currentCode)])
    }
    
    function downloadPowerPoint() {
        currentViz.showExportPowerPointDialog()
    }
    
    function onUndoClick() {
        currentViz.undoAsync()
    }
    
    function onRedoClick() {
        currentViz.redoAsync()
    }
    
    function onShareClick() {
        currentViz.showShareDialog()
    }
    
    function onDownloadClick() {
        currentViz.showDownloadDialog()
    }
}