import React from "react"
import { useState, useEffect } from "react"
import * as style from "../styles/Tooltip.module.scss"

export default function Tooltip(props: any) {

    const [elementId, setElementId] = useState<string>(props.elementId)
    const [content, setContent] = useState<string>(props.content)

    return (
        <div id={elementId + "Tooltip"} className={style.tooltip}>
            <p>{content}</p>
        </div>
    )
}

function onShowTooltip(callerId: string) {
    const tooltip = document.getElementById(callerId + "Tooltip")
    const caller = document.getElementById(callerId)
    if (!tooltip) return
    tooltip.style.opacity = "1"
    caller.addEventListener("mouseleave", onMouseLeave)

    function onMouseLeave() {
        tooltip.style.opacity = "0"
        caller.removeEventListener("mouseleave", onMouseLeave)
    }
}
export { onShowTooltip }