@use "global.module.scss" as *;

#mobileContainer {
    background-color: $primaryAccent;
    position: fixed;
    top:  105px;
    left: -240px;
    padding: 9px 21px 21px 21px;
    z-index: 1;
    border-radius: 15px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
    transition: all 0.1s ease-in-out;
}

#desktopContainer {
    background-color: white;
    padding: 0 0 0 12px;
    z-index: 1;
}

#sidebar {
    position: relative;
    min-width: 210px;
}

.icon {
    background-color: $primaryAccent;
    color: white;
    position: absolute;
    top: -21;
    right: -50;
    padding: 15px;
    font-size: 100%;
    width: 30px;
    height: 30px;
    z-index: -1;
    border-radius: 50%;
}