import React from 'react'
import { useState, useEffect } from 'react'
import * as Tableau from 'tableau-api-js'
import { isMobile } from 'react-device-detect'
import * as style from '../styles/TableauViz.module.scss'

let currentViz: Tableau.Viz
let currentCode: string
export { currentViz, currentCode }

export default function TableauViz(props: any) {

    const [index, setIndex] = useState(props.index)
    const [code, setCode] = useState(props.codes[index])
    const [vizHeight, setHeight] = useState(props.heights[index])
    const [vizUrl, setUrl] = useState(props.urls[index])
    const [vizWidth, setWidth] = useState(props.width)

    let vizReference: Tableau.Viz = null
    let isVizInitialized = false
    let currentUrl: string = vizUrl
    let placeholderDiv: HTMLElement
    let hasChanges = false

    useEffect(() => {
        placeholderDiv = document.getElementById(code)
        initializeTableauViz(vizHeight, vizUrl, code)
        window.addEventListener("mavQ:toggleTableauViz", handleToggleTableauVizEvent)
        window.addEventListener("mavQ:resetViz", handleResetVizEvent)
    }, [])

    return (
        <div id={code} className={style.placeholder}></div>
    )

    function handleToggleTableauVizEvent(event: any) {
        if (event.detail.code !== code) return
        if (event.detail.enabled == false) {
            placeholderDiv.style.position = "relative"
            placeholderDiv.style.opacity = "0"
            placeholderDiv.style.pointerEvents = "none"
            placeholderDiv.style.zIndex = "-1"
            return;
        }
        if (isVizInitialized) {
            if (currentUrl != vizUrl) {
                vizReference.dispose()
                initializeTableauViz(vizHeight, vizUrl, code)
            }
        }

        placeholderDiv.style.position = "absolute"
        placeholderDiv.style.opacity = "1"
        placeholderDiv.style.pointerEvents = "unset"
        placeholderDiv.style.zIndex = "0"
        placeholderDiv.parentElement.style.height = vizHeight + "px"

        currentCode = event.detail.code
        currentViz = vizReference

        window.dispatchEvent(new Event("resize"))
        placeholderDiv.scrollIntoView({ behavior: "smooth", block: "start", inline: "nearest" })

        if (hasChanges) window.dispatchEvent(new Event("mavQ:resetRed"))
        else window.dispatchEvent(new Event("mavQ:resetWhite"))
    }

    function handleResetVizEvent(event: any) {
        if (event.detail.code !== code) return
        hasChanges = false
        if (currentUrl != vizUrl) {
            const urlIndex = props.urls.indexOf(currentUrl)
            const newCode = props.codes[urlIndex]
            window.dispatchEvent(new CustomEvent("mavQ:toggleTableauViz", { detail: { code: newCode, enabled: true } }))
        }
        vizReference.dispose()
        initializeTableauViz(vizHeight, vizUrl, code)
    }

    function initializeTableauViz(height: number, url: string, code: string) {
        let tableOptions = {
            maxRows: 0,
            ignoreAliases: false,
            ignoreSelection: true,
            includeAllColumns: false
        }
        var options = {
            hideTabs: true,
            width: vizWidth + "px",
            height: height + "px",
            onFirstInteractive: async function () {
                isVizInitialized = true
                currentUrl = vizReference.getWorkbook().getActiveSheet().getUrl();
                if (currentCode == code) currentViz = vizReference
                if (isMobile) return
                // let sheet = viz.getWorkbook().getActiveSheet().getWorksheets().get("IE Expenditures and Payments")
                // await sheet.getUnderlyingTablesAsync().then(tables => {
                //     return sheet.getUnderlyingTableDataAsync(tables[0].getTableId(), tableOptions).then(getUnderlyingData)
                // })
            }
        }
        if (vizReference) vizReference.dispose()
        vizReference = new Tableau.Viz(placeholderDiv, url, options)
        vizReference.addEventListener('filterchange', onVizActionDetected);
        vizReference.addEventListener('marksselection', onVizActionDetected);
        vizReference.addEventListener('parametervaluechange', onVizActionDetected);
        vizReference.addEventListener('tabswitch', onTabSwitch);
    }

    function onVizActionDetected(event: any) {
        window.dispatchEvent(new Event("mavQ:resetRed"))
        hasChanges = true
    }

    function onTabSwitch(event: any) {
        currentUrl = vizReference.getWorkbook().getActiveSheet().getUrl();
        const urlIndex = props.urls.indexOf(currentUrl)
        const newHeight = props.heights[urlIndex]
        vizReference.setFrameSize(vizWidth, newHeight)
        placeholderDiv.parentElement.style.height = newHeight + "px"
        placeholderDiv.scrollIntoView({ behavior: "smooth", block: "start", inline: "nearest" })
        window.dispatchEvent(new CustomEvent("mavQ:tableauTabSwitch", { detail: { code: props.codes[urlIndex] } }))
    }

    async function getUnderlyingData(dataTable: any) {
        let data2d: any[][] = dataTable.getData()
        let columnArray = dataTable.getColumns().map(column => {
            return {
                title: column.getFieldName(),
                field: column.getFieldName()
            }
        })

        let dataArray = []
        data2d.forEach(row => {
            let obj: any = {}
            row.forEach((cell, index) => {
                obj[columnArray[index].field] = cell.formattedValue
            })
            dataArray.push(obj)
        })
        console.log(dataArray)
        console.log(columnArray)
        const eventDetails = { columns: columnArray, data: dataArray }
        const event = new CustomEvent("mavQ:recievedDataFromTableau", { detail: eventDetails })
        document.dispatchEvent(event)
    }
}