import React, { useEffect, useState } from "react"
import TableauViz from "./TableauViz"
import Footer from "./Footer"
import * as style from "../styles/Dashboard.module.scss"
import * as globalStyle from "../styles/global.module.scss"
import Sidebar from "./Sidebar"

let container: HTMLElement
let vizContainer: HTMLElement
let isFullscreen = false

export default function Dashboard(props: any) {

    const [codes, setCodes] = useState(props.codes)
    const [starredCodes, setStarredCodes] = useState(props.starredCodes) 
    const [tabNames, setTabNames] = useState(props.tabNames)
    const [heights, setHeights] = useState(props.heights)
    const [urls, setUrls] = useState(props.urls)
    const [downloads, setDownloads] = useState(props.downloads)
    const [width, setWidth] = useState(props.width)
    const [leftOffset, setLeftOffset] = useState(props.leftOffset)

    useEffect(() => {
        document.getElementById(style.wrapper).scrollIntoView()
        vizContainer = document.getElementById(style.vizContainer)
        vizContainer.style.left = `${leftOffset}px`
        vizContainer.style.margin =  `0 ${leftOffset}px 0 0`
        container = document.getElementById(style.container)
        container.addEventListener('fullscreenchange', onFullscreenChange)
        window.addEventListener("mavQ:onFullscreenClick", onFullscreenClick)
        window.addEventListener("resize", fixMargin)
        window.addEventListener("mavQ:vizContainerResize", handleVizContainerResizeEvent)
        window.addEventListener("mavQ:sidebarToggle", handleSidebarToggleEvent)
        window.addEventListener("mavQ:setLightTheme", handleSetLightThemeEvent)
        window.addEventListener("mavQ:setYellowTheme", handleSetYellowThemeEvent)
        window.dispatchEvent(new CustomEvent("mavQ:toggleTableauViz", { detail: { enabled: true, code: codes[0] } }))
        window.dispatchEvent(new Event("resize"))

        return () => {
            window.removeEventListener("resize", fixMargin)
        }
    }, [])

    function handleVizContainerResizeEvent(event: any) {
        vizContainer.style.height = `${event.detail.height}px`
    }

    function handleSidebarToggleEvent(event: any) {
        const sidebar = document.getElementById(style.sidebarContainer)
        if (event.detail.enabled) sidebar.style.width = "24px"
        else sidebar.style.width = "222px"
    }

    function handleSetLightThemeEvent() {
        document.getElementById(style.tableauBlocker).style.backgroundColor = "white"
        document.getElementById(style.sidebarContainer).style.backgroundColor = "white"
        document.getElementById(style.vizContainer).style.backgroundColor = "white"
    }

    function handleSetYellowThemeEvent() {
        document.getElementById(style.tableauBlocker).style.backgroundColor = globalStyle.lightYellow
        document.getElementById(style.sidebarContainer).style.backgroundColor = globalStyle.lightYellow
        document.getElementById(style.vizContainer).style.backgroundColor = globalStyle.lightYellow
    }

    return (
        <div id={style.wrapper}>
            <div id={style.container}>
                <div id={style.mainContent}>
                    <div id={style.topContainer}>
                        <div id={style.sidebarContainer}><Sidebar codes={codes} tabNames={tabNames} starredCodes={starredCodes} downloads={downloads} /></div>
                        <div id={style.vizContainer}>
                            {
                                codes.map((code: string, index: number) => {
                                    return <TableauViz key={index} id={code + " Viz"} index={index} codes={codes} heights={heights} urls={urls} width={width}/>
                                })
                            }
                        </div>
                    </div>
                    <div id={style.tableauBlocker}></div>
                </div>
                <Footer />
            </div>
        </div>
    )
    let scale = .8
    function fixMargin() {
        const minScale = .08
        const maxScale = .8
        const width = container.clientWidth
        const height = container.clientHeight
        const windowWidth = window.innerWidth
        // console.log(width)
        
        scale = windowWidth / width
        if (scale < minScale) scale = minScale
        else if (scale > maxScale) scale = maxScale
        else scale = scale
    
        container.style.scale = scale + ""
        container.style.marginRight = "-" + (width - width * scale) + "px"
        container.style.marginBottom = "-" + (height - height * scale) + "px"
    }
    
    function onFullscreenClick() {
        if (document.fullscreenElement) {
            document.exitFullscreen()
            return
        }
        window.removeEventListener("resize", fixMargin)
        container.requestFullscreen()
    }
    
    function onFullscreenChange() {
        const isContainerFullscreen: boolean = document.fullscreenElement ? true : false
        if (isContainerFullscreen) {
            container.style.alignItems = "center"
            container.style.overflow = "scroll"
            container.style.scrollBehavior = "unset"
            container.style.scale = "unset"
            isFullscreen = true
            return
        }
        container.style.alignItems = "unset"
        container.style.overflow = "hidden"
        container.style.scrollBehavior = "none"
        isFullscreen = false
        container.scrollTop = 0
        window.addEventListener("resize", fixMargin)
    }
}
