import React from "react"
import { useState, useEffect } from "react"
import * as style from "../styles/NavBar.module.scss"
import * as globalStyle from "../styles/global.module.scss"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faChevronRight } from "@fortawesome/free-solid-svg-icons"
import { THEMES, currentTheme } from "./App"

let lastCheckedInput: HTMLInputElement = null;

function NavButton(props: any) {

    const [code, setCode] = useState<string>(props.code)
    const [tabName, setTabName] = useState<string>(props.tabName)
    const [isStarred, setIsStarred] = useState<boolean>(props.isStarred)

    useEffect(() => {
        if (code.split("_")[1] === "01") {
            lastCheckedInput = document.getElementById(code + " checkbox") as HTMLInputElement
            lastCheckedInput.checked = true
        }
        if (code === "ICIP_01") document.getElementById(code + " tab").style.backgroundColor = globalStyle.darkGreen
        if (code === "ICIP_02") document.getElementById(code + " tab").style.backgroundColor = globalStyle.darkGreen
        if (code === "ICIP_03") document.getElementById(code + " tab").style.backgroundColor = globalStyle.yellow
        if (code === "ICIP_04") document.getElementById(code + " tab").style.backgroundColor = globalStyle.yellow
        window.addEventListener("mavQ:tableauTabSwitch", checkNavButton)
    }, [])

    return (
        <>
            <input id={code + " checkbox"} type="checkbox" className={style.buttonInput}></input>
            <div id={code + " navButton"} className={style.navButton} onClick={handleNavButtonClick}>
                <span id={code + " tab"} className={style.buttonTabColor}></span>
                <span className={style.buttonText}>
                    <div className={style.buttonCode}>
                        {code}{isStarred ? <p className={style.buttonStar}></p> : ""}
                    </div>
                    <p className = {style.buttonName}>{tabName}</p>
                </span>
                <span className={style.buttonArrowContainer}>
                    <FontAwesomeIcon icon={faChevronRight} className={style.buttonArrow} />
                </span>
            </div>
        </>
    )

    function handleNavButtonClick(event: any) {
        let lastVizCode = lastCheckedInput.id.split(" ")[0]
        let code = event.target.id.split(" ")[0]

        lastCheckedInput.checked = false
        window.dispatchEvent(new CustomEvent("mavQ:toggleTableauViz", { detail: { enabled: false, code: lastVizCode } }))

        let newCheckedInput = document.getElementById(code + " checkbox") as HTMLInputElement
        newCheckedInput.checked = true
        lastCheckedInput = newCheckedInput
        window.dispatchEvent(new CustomEvent("mavQ:toggleTableauViz", { detail: { enabled: true, code: code } }))

        if (code === "ICIP_03" || code === "ICIP_04")
            window.dispatchEvent(new CustomEvent("mavQ:setYellowTheme"))
        else if (currentTheme !== THEMES.light)
            window.dispatchEvent(new CustomEvent("mavQ:setLightTheme"))
    }

    function checkNavButton(event: any) {
        let code = event.detail.code
        let newCheckedInput = document.getElementById(code + " checkbox") as HTMLInputElement
        lastCheckedInput.checked = false
        newCheckedInput.checked = true
        lastCheckedInput = newCheckedInput
    }
}

export default function Navbar(props: any) {

    const [codes, setCodes] = useState(props.codes)
    const [tabNames, setTabNames] = useState(props.tabNames)
    const [starredCodes, setStarredCodes] = useState<string[]>(props.starredCodes)

    return (
        <div className={style.container}>
            {codes.map((code: string, index: number) => {
                if (starredCodes)
                    return <NavButton key={index} code={code} tabName={tabNames[index]} isStarred={starredCodes.includes(code)} />
                else
                    return <NavButton key={index} code={code} tabName={tabNames[index]} isStarred={false} />
            })}
        </div>
    )
}